












import Vue from 'vue';
import _ from 'lodash';

export default Vue.extend({
    props: {
      until: {
        type: [Boolean, Function],
        default: true
      },
      text: {
        type: String
      }
    },
    computed: {
      isComplete(): boolean {
        if (_.isFunction(this.until)) {
          return this.until();
        }

        return Boolean(this.until);
      }
    }
});
