















import Vue from 'vue';

interface IStep {
    key: string | number,
}

function firstOrDefault<T>(items: T[], predicate: (item: T) => boolean): T | null {
    for (var i = 0; i < items.length; i++) {
        const result: boolean = predicate(items[i]);
        if (result) return items[i];
    }

    return null;
}

export default Vue.extend({
    props: {
        steps: {
            type: Array as () => IStep[],
            default: () => []
        },
        active: {
            type: [String, Number],
            default: null,
        }
    },
    computed: {
        activeStep(): IStep {
            const match = firstOrDefault(this.steps as IStep[], item => item.key === this.active);
            return match || this.steps[0];
        }
    }
});
