















































































import Vue from 'vue';

export default Vue.extend({
    mounted() {
        let counter = 0;

        setInterval(() => {
            const max = this.steps.length - 1;
            counter = counter === max ? 0 : counter + 1;
            this.active = this.steps[counter].key;
        }, 3000);
    },
    data() {
        return {
            steps: [
                { key: 'first' },
                { key: 'second' },
                { key: 'third' },
            ],
            active: 'first',
        };
    },
    methods: {
        clickHandler(e: any): void {
            window.alert(e);
        }
    }
});
