


































































































































































































































































import Vue from 'vue';
import { required, minLength } from 'vuelidate/lib/validators';

export default Vue.extend({
  data: () => ({
    name: '',
    witherror: '',
    long: '',
    years: null,
    colors: null,
    story: '',
    password: '',
    checked: true,
    yesno: null,
    alwayserror: '',
    gender: '',
  }),
  computed: {
    noCharacters(): string {
      return this.long.length + ' tegn.';
    }
  },
  filters: {
    emptyDisplay(value: string | null): string {
      if (value == null || value === '') return "[empty]";
      return value;
    }
  },
  validations: {
    witherror: {
      required,
      minLength: minLength(3)
    }
  },
});
