














import Vue from 'vue';

export default Vue.extend({
    inheritAttrs: true,
    props: {
        id: {
            type: String,
            required: true,
        },
        value: {
            type: Boolean,
            required: true,
        },
        noEasyEscape: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        computedShow: {
            get() {
                return (this as any).value;
            },
            set(nValue) {
                (this as any).$emit('input', nValue);
            }
        }
    }
});
