





















import Vue from 'vue';

interface IComputedStyles {
    width?: string;
    cursor?: string;
}

export default Vue.extend({
    props: {
        white: {
            type: Boolean,
            default: false,
        },
        full: {
            type: Boolean,
            default: false,
        },
        width: {
            type: Number,
            default: 0
        },
        vertical: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        styles(): IComputedStyles {
            let result : IComputedStyles = {};

            if (this.width > 0) {
                result.width = this.width + 'px';
            }

            if (this.$listeners.click) {
                result.cursor = 'pointer';
            }

            return result;
        }
    }
});
