





















































































import Vue from 'vue';

export default Vue.extend({
    mounted() {
        this.reload();
    },
    data() {
        return {
            loaded: false
        }
    },
    methods: {
        reload(): void {
            this.loaded = false;
            
            const self = this;
            setTimeout(() => self.loaded = true, 2000);
        }
    }
});
