














































import Vue from 'vue';
import _ from 'lodash';

interface IKey {
    key: string;
}

interface IData {
    step: string,
    auto: boolean,
    steps: IKey[]
}

export default Vue.extend({
    mounted() {
        setInterval(() => {
            if (this.auto) {
                const steps = this.steps as IKey[];
                const maxStep = _.maxBy(steps, item => item.key);

                if (maxStep == null) return;

                const max = parseInt(maxStep.key, 10);
                const current = parseInt(this.step as string, 10);

                const next = current === max ? 1 : current + 1;
                this.step = next.toString();
            }
        }, 10000);
    },
    data(): IData {
        return {
            step: '1',
            auto: true,
            steps: [
                { key: '1' },
                { key: '2' },
                { key: '3' },
            ]
        };
    },
    methods: {
        stepClick(key: string): void {
            this.auto = false;
            this.step = key;
        }
    }
})
