
































































































































































import Vue from 'vue';
import moment from 'moment';

interface IData {
    minuteDiff: number | null;
}

export default Vue.extend({
    mounted() {

        const calcDiff = () => {
            if (this.$auth != null && this.$auth.expiresAt != null) {
                this.minuteDiff = moment(this.$auth.expiresAt).diff(moment(), 'm');
            }
        };

        calcDiff();

        setInterval(calcDiff, 5000);
    },
    data(): IData {
        return {
            minuteDiff: null,
        }
    },
});
