









































































































































import Vue from "vue";


export default Vue.extend({
  data: function() {
    return {
        activeStep: 'one',
        steps: [
            {
                key: 'one',
                title: 'First step',
                icon: 'info-circle',
            },
            {
                key: 'two',
                title: 'Second step',
                icon: 'comment',
            },
            {
                key: 'three',
                title: 'Third step',
                icon: 'hand-point-right',
            },
        ],
    };
  },
  methods: {
    buttonPress(type: string) {
      window.alert(`Clicked button: ${type}`);
    },
    stepClick(step: any): void {
        window.alert(`Clicked step ${step.key}.`);
    }
  }
});
