























































import Vue, { Component } from 'vue';

interface IStep {
    key: string,
    title: string,
    icon?: string,
}

interface IProgress {
    activeStep: IStep,
    activeIndex: number,
    isFirst: boolean,
    isLast: boolean,
    nextKey: string | null,
    previousKey: string | null,
}

export default Vue.extend({
    props: {
        active: String,
        steps: {
            type: Array as () => IStep[],
            default: () => [],
        },
        previousText: {
            type: String,
            default: 'Tilbage',
        },
        continueText: {
            type: String,
            default: 'Næste',
        },
        displayNavigation: {
            type: Boolean,
            default: true,
        },
        displaySubmit: {
            type: Boolean,
            default: true,
        },
        displayProgress: {
            type: Boolean,
            default: true,
        },
        keepAlive: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        activeStep(): IStep {
            if (this.active == null) {
                return this.steps[0];
            }

            return this.getStep(this.active) as IStep;
        },
        activeIndex(): number {
            return this.steps.indexOf(this.activeStep);
        },
        isFirst(): boolean {
            return this.activeIndex === 0;
        },
        isLast(): boolean {
            return this.activeIndex === this.steps.length - 1;
        },
        nextKey(): string | null {
            if (!this.isLast) {
                const index = this.getIndex(this.activeStep.key) + 1;
                return this.getStepAtIndex(index).key;
            }

            return null;
        },
        previousKey(): string | null {
            if (!this.isFirst) {
                const index = this.getIndex(this.activeStep.key) - 1;
                return this.getStepAtIndex(index).key;
            }

            return null;
        },
        progress(): IProgress {
            return { 
                activeStep: this.activeStep,
                activeIndex: this.activeIndex,
                isFirst: this.isFirst,
                isLast: this.isLast,
                nextKey: this.nextKey,
                previousKey: this.previousKey
            };
        }
    },
    methods: {
        getStep(key: string): IStep | null {
            const result = this.steps.filter(x => x.key === key);
            if (result.length > 0) return result[0];
            return null;
        },
        getIndex(key: string): number {
            const step = this.getStep(key);

            if (step != null) {
                return this.steps.indexOf(step);
            }

            return -1;
        },
        getStepAtIndex(index: number): IStep {
            return this.steps[index];
        }
    }
});
