




















import Vue from 'vue';

import { BContainer, BRow, BCol } from 'bootstrap-vue';

export default Vue.extend({
    components: {
        'b-container': BContainer,
        'b-row': BRow,
        'b-col': BCol,
    },
    props: {
        theme: {
            type: String,
            default: 'white',
        },
        verticalFill: Boolean,
        noContainer: Boolean,
        slim: Boolean,
        fluid: Boolean,
        noPad: Boolean,
        suppressDivider: Boolean,
        fullWidth: Boolean,
    },
});
