








import Vue from 'vue';
import { BContainer } from 'bootstrap-vue';

const variants : string[] = ['darkblue', 'lightgrey'];

export default Vue.extend({
    components: {
        'b-container': BContainer,
    },
    props: {
        variant: {
            type: String,
            default: 'darkblue',
            validator: (value: string) => variants.indexOf(value) !== -1,
        }
    }
})
